  display: flex;
  justify-content: space-between;
  align-items: center;
}
.edit-task-button {
  margin-right: .25rem ;
} 

#navbar-right {
  display: flex;
}

#signed-in-dropdown {
  margin-right: 1rem;
}

.add-task-button {
  margin: 0 1rem;
}

.error {
  color: red;
  /* float: right; */
  flex-grow: 2;
  text-align: center;
}

#login-container {
  display: flex;
  justify-content: center;
}

#login-form {
  width: 50%;
}

#dropdown-menu-align-right {
  border-radius: 0;
}

.button-error-container {
  display: flex;
  align-items: center;
}

.delete-goal-button {
  float: right;
}



#main-title-button {
  margin: 0 1rem;
}

.main {
  color: whitesmoke;
  background-color: #333;
  /* color: whitesmoke;  */
  padding: 3%; 
  min-height: 80vh; 
  width: 100%;
}

#main-title-container{
  display: flex;
  justify-content: center;
  align-items: center;
}
.drop-container {
  width: 100px;
  height: 100px;
  border: dotted #333 1px;
  text-align: center;
}

h2 {
  text-align: center;
}

body {
  font-family: 'Chelsea Market', cursive;
  background-color: #333;
}

.task {
  margin: 10px 0;
  opacity: .9;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 1px;

  color: #333;
  /* margin: '10px'; */
}


.task-body {
  color: #333;
  background-color: whitesmoke;
  display: flex;
  align-items: center;
  padding: .25rem;
  justify-content: space-between;
}

.task-body-complete {
  color: #333;
  background-color: whitesmoke;
  padding-left: 1rem;
}

.task .check {
  float: left;
  margin-left: 1rem;
}

.goal {
  -webkit-user-select: "none";
      -ms-user-select: "none";
          user-select: "none";
  background-color: #333;
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: whitesmoke;
}

.goal-listgroup {
  padding: 1px;
  margin-bottom: .2rem;  
}

.goal-name {
  /* border: #333 1px solid; */
  text-align: center;
  width: 25%;
  /* padding: .3rem; */
  float: left;
  /* border-radius: 3px; */
}

.progress {
  width: 50%;
  float: center;
  /* border: solid red 1px; */
  /* flex-grow: 2; */
}

.goal-due {
  float: right;
  width: 25%;
  color: white;
  /* border: solid red 1px; */

  text-align: center;
}

